import { useState, useEffect } from "react";
import { Grid, CircularProgress } from "@material-ui/core";
import CardMat from "components/Card/Card.js";
import CardHeaderMat from "components/Card/CardHeader.js";
import Snackbar from "components/Snackbar/Snackbar.js";
import { authFetch } from "auth";
import AddInventaireDialog from "../components/Dialog/AddInventaireDialog";
import DateChart from "components/DateChart";

export default function Stocks() {
  const [stocks, setStocks] = useState([]);
  const [alimentList, setAlimentList] = useState([]);

  const [maj, setMaj] = useState(true);

  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  let dateOneYear = new Date();
  dateOneYear.setMonth(dateOneYear.getMonth() - 12);

  useEffect(() => {
    const url = "/api/stocks/" + dateOneYear.toISOString().substring(0, 10);
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setStocks(response.stocks);
        setAlimentList(response.alimentList);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [maj]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  return (
    <Grid container spacing={1} justify="center" alignItems="center">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      {alimentList.length !== 0 ? (
        <Grid container item xs={12} justify="center" alignItems="center">
          <AddInventaireDialog
            alimentsList={alimentList}
            maj={maj}
            setMaj={setMaj}
            handleSnackbar={handleSnackbar}
          />
        </Grid>
      ) : null}
      {stocks.length !== 0 ? (
        stocks.map((s, key) => (
          <Grid item key={key + 100} xs={4}>
            <CardMat key={key + 1000}>
              <CardHeaderMat key={key + 10000}>
                <DateChart
                  key={key + 100000}
                  title={s.typeAlimentLibelle}
                  data={[
                    {
                      name: "Stock",
                      data: stocks[key].stock.map((s) => [
                        new Date(s.Date).getTime(),
                        s.Stock,
                      ]),
                    },
                    {
                      name: "Alimentacion",
                      data: stocks[key].stock.map((s) => [
                        new Date(s.Date).getTime(),
                        s.Alimentation,
                      ]),
                    },
                    {
                      name: "Entrada",
                      data: stocks[key].stock.map((s) => [
                        new Date(s.Date).getTime(),
                        s.Entr√©e,
                      ]),
                    },
                    {
                      name: "Ventas",
                      data: stocks[key].stock.map((s) => [
                        new Date(s.Date).getTime(),
                        s.Ventes,
                      ]),
                    },
                    {
                      name: "Inventario",
                      data: stocks[key].stock.map((s) => [
                        new Date(s.Date).getTime(),
                        s.Ajustement,
                      ]),
                    },
                  ]}
                  dateOption={{
                    xStartDate: new Date(
                      dateOneYear.toISOString().substring(0, 10)
                    ).getTime(),
                    colors: [
                      "#008FFB",
                      "#ffa726",
                      "#00e396",
                      "#caa0ff",
                      "#ef5350",
                    ],
                    theme: "light",
                  }}
                />
              </CardHeaderMat>
            </CardMat>
          </Grid>
        ))
      ) : (
        <Grid
          style={{ height: "80%" }}
          container
          spacing={2}
          alignItems="center"
          justify="center"
        >
          <Grid item>
            <CircularProgress
              style={{
                display: "flex",
                height: "150px",
                width: "150px",
              }}
            />
          </Grid>
        </Grid>
      )}
    </Grid>
  );
}
