import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { Grid } from "@material-ui/core";

export default function FormDialog({
  espece,
  lot,
  handleSnackbar,
  setMaj,
  maj,
}) {
  const [open, setOpen] = useState(false);
  const [semis, setSemis] = useState({
    date: "",
    poids: "",
    quantitelb: "",
    grpoisson: "",
    commentaire: "",
    quantite: "",
  });

  const handleConfirm = () => {
    console.log(lot, semis);
    let quantite = 0;
    let poids = 0;
    if (espece.substring(0, 10) === "Postlarves") {
      quantite = parseFloat(semis.quantite);
      poids = parseFloat(semis.quantite) / parseFloat(semis.quantitelb);
    } else if (espece.substring(0, 9) === "Juv√©niles") {
      quantite = parseFloat(semis.quantitelb) * parseFloat(semis.poids);
      poids = parseFloat(semis.poids);
    } else if (semis.poids === "") {
      quantite = parseFloat(semis.quantite);
      poids = (parseFloat(semis.quantite) * parseFloat(semis.grpoisson)) / 454;
    } else if (semis.grpoisson === "") {
      quantite = parseFloat(semis.quantite);
      poids = parseFloat(semis.poids);
    } else {
      console.log(semis);
    }
    var url = "/api/semis";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        ...semis,
        date:
          semis.date.substring(6, 10) +
          "-" +
          semis.date.substring(3, 5) +
          "-" +
          semis.date.substring(0, 2),
        quantite: quantite,
        poids: poids,
        lot: lot,
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    setOpen(false);
    setSemis({
      date: "",
      typePoids: "",
      poids: "",
      grpoisson: "",
      quantitelb: "",
      commentaire: "",
    });
  };

  return (
    <div>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="secondary"
        onClick={() => setOpen(true)}
      >
        A√±adir siembra
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">A√±adir una siembra</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Elija la fecha de siembra y la cantitad.
          </DialogContentText>
          <Grid container spacing={1} justify="center" alignItems="center">
            <Grid item xs={12}>
              <TextField
                required
                label="Date dd/MM/yyyy"
                type="text"
                value={semis.date}
                onChange={(e) => setSemis({ ...semis, date: e.target.value })}
              />
            </Grid>
            <Grid item xs={12}>
              {espece &&
              (espece.substring(0, 10) === "Postlarves" ||
                espece.substring(0, 7) === "Tilapia" ||
                espece.substring(0, 6) === "Carpes" ||
                espece.substring(0, 9) === "Colossoma") ? (
                <TextField
                  required
                  label="Cantidad total"
                  type="text"
                  value={semis.quantite}
                  onChange={(e) =>
                    setSemis({
                      ...semis,
                      quantite: e.target.value,
                    })
                  }
                />
              ) : (
                <TextField
                  required
                  label="Peso total"
                  type="text"
                  value={semis.poids}
                  onChange={(e) =>
                    setSemis({
                      ...semis,
                      poids: e.target.value,
                    })
                  }
                />
              )}
            </Grid>
            <Grid item xs={12}>
              {espece &&
              (espece.substring(0, 7) === "Tilapia" ||
                espece.substring(0, 5) === "Carpe" ||
                espece.substring(0, 9) === "Colossoma") ? (
                <TextField
                  required
                  label="Gr par poissons"
                  type="text"
                  value={semis.grpoisson}
                  onChange={(e) =>
                    setSemis({
                      ...semis,
                      grpoisson: e.target.value,
                    })
                  }
                />
              ) : (
                <TextField
                  required
                  label="Cantitad por libras"
                  type="text"
                  value={semis.quantitelb}
                  onChange={(e) =>
                    setSemis({
                      ...semis,
                      quantitelb: e.target.value,
                    })
                  }
                />
              )}
            </Grid>
            <Grid item xs={12}>
              {espece &&
              (espece.substring(0, 7) === "Tilapia" ||
                espece.substring(0, 5) === "Carpe" ||
                espece.substring(0, 9) === "Colossoma") ? (
                <TextField
                  required
                  label="Peso total"
                  type="text"
                  value={semis.poids}
                  onChange={(e) =>
                    setSemis({
                      ...semis,
                      poids: e.target.value,
                    })
                  }
                />
              ) : null}
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Comentario"
                type="text"
                value={semis.commentaire}
                onChange={(e) =>
                  setSemis({ ...semis, commentaire: e.target.value })
                }
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            color="primary"
          >
            Annuler
          </Button>
          <Button onClick={handleConfirm} color="primary">
            Ajouter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
