import { useState, useEffect } from "react";
import {
  Button,
  CardHeader,
  Card,
  CardContent,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  TextField,
} from "@material-ui/core";
import Snackbar from "components/Snackbar/Snackbar.js";
import { authFetch } from "auth";
import Clients from "../components/Clients";
import AddClientDialog from "components/Dialog/AddClientDialog";

export default function VenteAliment() {
  const [newVente, setNewVente] = useState({
    typeAliment: 2,
    date: "",
    clientId: 1,
    quantite: "",
    commentaire: "",
  });
  const [clientsList, setClientsList] = useState([]);
  const [typeAlimentList, setTypeAlimentList] = useState([]);
  const [maj, setMaj] = useState(true);

  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  useEffect(() => {
    var url = "/api/ventes/aliments";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setTypeAlimentList(
          response.typeAliment.sort((a, b) => (a[1] > b[1] ? 1 : -1))
        );
        setClientsList(response.clients.sort((a, b) => (a[1] > b[1] ? 1 : -1)));
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [maj]);

  const handleConfirm = () => {
    var url = "/api/ventes/aliments";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        ...newVente,
        date:
          newVente.date.substring(6, 10) +
          "-" +
          newVente.date.substring(3, 5) +
          "-" +
          newVente.date.substring(0, 2),
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
        setMaj(~maj);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    setNewVente({
      typeAliment: 2,
      date: "",
      clientId: 1,
      quantite: "",
      commentaire: "",
    });
  };

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  return (
    <Grid container spacing={1} justify="center" alignItems="center">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid item>
        <Card>
          <CardHeader
            title="A√±adir una venta"
            action={
              clientsList.length === 0 ? (
                <AddClientDialog maj={maj} setMaj={setMaj} />
              ) : null
            }
          />
          <CardContent>
            <Grid container spacing={1} justify="center" alignItems="center">
              <Grid item>
                <FormControl style={{ minWidth: "150px" }}>
                  <InputLabel id="Client-label">Client</InputLabel>
                  <Select
                    labelId="Client-label"
                    id="Client-select"
                    value={newVente.clientId}
                    onChange={(e) =>
                      setNewVente({
                        ...newVente,
                        clientId: e.target.value,
                      })
                    }
                  >
                    {clientsList.map((c, key) => (
                      <MenuItem key={key} value={c[0]}>
                        {c[1]}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
              <Grid item>
                <FormControl style={{ minWidth: "150px" }}>
                  <InputLabel id="alimento-label">Typo de alimento</InputLabel>
                  <Select
                    labelId="alimento-label"
                    id="alimento-select"
                    value={newVente.typeAliment}
                    onChange={(e) =>
                      setNewVente({
                        ...newVente,
                        typeAliment: e.target.value,
                      })
                    }
                  >
                    {typeAlimentList.map((c, key) => (
                      <MenuItem key={key} value={c[0]}>
                        {c[1]}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>
              <Grid item>
                <TextField
                  required
                  label="Fecha dd/MM/yyyy"
                  type="text"
                  value={newVente.date}
                  onChange={(e) =>
                    setNewVente({ ...newVente, date: e.target.value })
                  }
                />
              </Grid>
              <Grid item>
                <TextField
                  required
                  label="Cantidad"
                  type="number"
                  value={newVente.quantite}
                  onChange={(e) =>
                    setNewVente({
                      ...newVente,
                      quantite: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item>
                <TextField
                  label="Comentario"
                  type="text"
                  value={newVente.commentaire}
                  onChange={(e) =>
                    setNewVente({
                      ...newVente,
                      commentaire: e.target.value,
                    })
                  }
                />
              </Grid>
              <Grid item>
                <Button
                  onClick={handleConfirm}
                  variant="contained"
                  color="primary"
                >
                  Confirmar
                </Button>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Grid>
      <Grid item xs={12}>
        {clientsList.length !== 0 ? (
          <Clients
            clientsList={clientsList.sort((a, b) => (a[0] > b[0] ? 1 : -1))}
            maj={maj}
            setMaj={setMaj}
          ></Clients>
        ) : null}
      </Grid>
    </Grid>
  );
}
